import React from "react";
import { Card, CardContent } from "@material-ui/core";
import { Divider } from "semantic-ui-react";
import { isMobile } from "react-device-detect";

import "./Profile.css";
import SocialMedia from "../socialmedia/SocialMedia";

class Profile extends React.Component {
  state = { width: window.innerWidth };

  componentWillMount() {
    window.addEventListener("resize", this.handleWindowSizeChange);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.handleWindowSizeChange);
  }

  handleWindowSizeChange = () => {
    this.setState({ width: window.innerWidth });
  };

  renderDesktopView = () => {
    return (
      <Card className="profile-container">
        <CardContent>
          <div>
            <img
              className="avatar image"
              alt="Profile"
              src="https://raw.githubusercontent.com/escapeeescudo/Sketches/master/divyansh.jpg"
            />

            <div className="name text">Divyansh Chaudhary</div>

            <div className="desc text">
              I dream of sketching and then I sketch my dream.
            </div>

            <div className="country text">Bengaluru, INDIA</div>
          </div>

          <SocialMedia />

          <Divider horizontal className="divider">
            Or
          </Divider>

          <div className="contact-me-head text">Contact Me</div>
          <div className="contact-me-desc text">
            <a href="mailto:someone@example.com">escapeeescudo@gmail.com</a>
          </div>

          <div className="copyright text">
            <b>Copyright © 2020 escapeeescudo. All rights reserved.</b>
          </div>
        </CardContent>
      </Card>
    );
  };

  renderMobileView = () => {
    return (
      <div className="profile-root-mobile">
        <Card className="profile-container-mobile">
          <CardContent>
            <div className="wrapper">
              <img
                className="avatar mobile-image"
                alt="Profile"
                src="https://raw.githubusercontent.com/escapeeescudo/Sketches/master/divyansh.jpg"
              />
              <div className="user-details">
                <div className="name text">Divyansh Chaudhary</div>
                <div className="desc text">
                  I dream of sketching and then I sketch my dream.
                </div>
                <div className="country text">Bengaluru, INDIA</div>
              </div>
              <div className="more-user-details">
                <SocialMedia />
                <Divider horizontal className="divider">
                  OR
                </Divider>
                <div className="contact-me-head text">Contact Me</div>
                <div className="contact-me-desc text">
                  <a href="mailto:someone@example.com">
                    escapeeescudo@gmail.com
                  </a>
                </div>
              </div>
            </div>
          </CardContent>
        </Card>
      </div>
    );
  };

  render() {
    if (isMobile) {
      return this.renderMobileView();
    } else {
      return this.renderDesktopView();
    }
  }
}

export default Profile;
