import React from "react";
import { Image, List } from "semantic-ui-react";
import { isMobile } from "react-device-detect";

import facebook from "../../images/facebook.svg";
import instagram from "../../images/instagram.svg";
import linkedin from "../../images/linkedin.svg";
import behance from "../../images/behance.svg";

import "./SocialMedia.css";

class SocialMedia extends React.Component {
  state = { width: window.innerWidth };

  componentWillMount() {
    window.addEventListener("resize", this.handleWindowSizeChange);
  }

  componentWillUnmount() {
    window.removeEventListener("resize", this.handleWindowSizeChange);
  }

  handleWindowSizeChange = () => {
    this.setState({ width: window.innerWidth });
  };

  renderDesktopView = () => {
    return (
      <div className="soial-media-conatiner">
        <List horizontal size="huge">
          <List.Item>
            <a
              href="https://www.instagram.com/escapeeescudo/"
              target="_blank"
              rel="noopener noreferrer"
            >
              <Image avatar src={instagram} />
            </a>
          </List.Item>
          <List.Item>
            <a
              href="https://www.behance.net/divyanshchaudhary"
              target="_blank"
              rel="noopener noreferrer"
            >
              <Image avatar src={behance} />
            </a>
          </List.Item>
          <List.Item>
            <a
              href="https://www.linkedin.com/in/divyanshchaudhary/"
              target="_blank"
              rel="noopener noreferrer"
            >
              <Image avatar src={linkedin} />
            </a>
          </List.Item>
          <List.Item>
            <a
              href="https://www.facebook.com/divyansh.chaudhary.18/"
              target="_blank"
              rel="noopener noreferrer"
            >
              <Image avatar src={facebook} />
            </a>
          </List.Item>
        </List>
      </div>
    );
  };

  renderMobileView = () => {
    return (
      <div className="soial-media-conatiner-mobile">
        <List horizontal size="huge">
          <List.Item>
            <a
              href="https://www.instagram.com/escapeeescudo/"
              target="_blank"
              rel="noopener noreferrer"
            >
              <Image avatar src={instagram} />
            </a>
          </List.Item>
          <List.Item>
            <a
              href="https://www.behance.net/divyanshchaudhary"
              target="_blank"
              rel="noopener noreferrer"
            >
              <Image avatar src={behance} />
            </a>
          </List.Item>
          <List.Item>
            <a
              href="https://www.linkedin.com/in/divyanshchaudhary/"
              target="_blank"
              rel="noopener noreferrer"
            >
              <Image avatar src={linkedin} />
            </a>
          </List.Item>
          <List.Item>
            <a
              href="https://www.facebook.com/divyansh.chaudhary.18/"
              target="_blank"
              rel="noopener noreferrer"
            >
              <Image avatar src={facebook} />
            </a>
          </List.Item>
        </List>
      </div>
    );
  };

  render() {
    if (isMobile) {
      return this.renderMobileView();
    } else {
      return this.renderDesktopView();
    }
  }
}

export default SocialMedia;
